.image-list {
  display: grid;
  grid-template-columns: repeat(
    auto-fill,
    minmax(250px, 1fr)
  ); /* sets boxes each of min width of 250px and max 1fr */
  grid-gap: 0px 10px; /* sets gap of between boxes (gap bw row | gap bw column) */
  grid-auto-rows: 10px; /* sets min height of each row */
  text-align: center;
}

.image-list img {
  width: 100%;
}

@media (min-width: 762px) and (max-width: 991px) {
  .image-list img {
    width: 100%px;
  }
  .image-list {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 0px 10px; /* sets gap of between boxes (gap bw row | gap bw column) */
  }
}
